<template>
  <div class="container">
    <h1>My Todo List</h1>
    <div class="create-post">
      <label for="create-post">Write your task here </label>
      <input class="myInput" type="text" id="create-post" v-model="text" placeholder="Write Something for your todo list">
      <button @click="createPost">Submit!</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="posts-container">
      <div class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id">
        Created at {{ `${post.createdAt.getMonth()}/${post.createdAt.getDate()}/${post.createdAt.getFullYear()}` }}
        <p class="text">{{ post.text }}<button @click="deletePost(post._id)"> X </button></p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';
import { reactive } from 'vue';

export default {
  name: 'PostComponent',
  data() {
     return {
      posts: reactive([]),
      error: '',
      text: ''
     }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text);
      this.post = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.post = await PostService.getPosts();
    }
  }

}
</script>

